'use strict';

var pull = require('pull-stream/pull');

var values = require('pull-stream/sources/values');

var pullLP = require('pull-length-prefixed');

var varint = require('varint');

function lsHandler(self, conn) {
  var protos = Object.keys(self.handlers).filter(function (key) {
    return key !== 'ls';
  });
  var nProtos = protos.length; // total size of the list of protocols, including varint and newline

  var size = protos.reduce(function (size, proto) {
    var p = Buffer.from(proto + '\n');
    var el = varint.encodingLength(p.length);
    return size + el;
  }, 0);
  var buf = Buffer.concat([Buffer.from(varint.encode(nProtos)), Buffer.from(varint.encode(size)), Buffer.from('\n')]);
  var encodedProtos = protos.map(function (proto) {
    return Buffer.from(proto + '\n');
  });
  pull(values([buf].concat(encodedProtos)), pullLP.encode(), conn);
}

module.exports = lsHandler;