'use strict';

var pull = require('pull-stream/pull');

var handshake = require('pull-handshake');

var constants = require('./constants');

var PROTOCOL = constants.PROTOCOL;
var PING_LENGTH = constants.PING_LENGTH;

var debug = require('debug');

var log = debug('libp2p-ping');
log.error = debug('libp2p-ping:error');

function mount(swarm) {
  swarm.handle(PROTOCOL, function (protocol, conn) {
    var stream = handshake({
      timeout: 0
    });
    var shake = stream.handshake; // receive and echo back

    function next() {
      shake.read(PING_LENGTH, function (err, buf) {
        if (err === true) {
          // stream closed
          return;
        }

        if (err) {
          return log.error(err);
        }

        shake.write(buf);
        return next();
      });
    }

    pull(conn, stream, conn);
    next();
  });
}

function unmount(swarm) {
  swarm.unhandle(PROTOCOL);
}

exports = module.exports;
exports.mount = mount;
exports.unmount = unmount;