'use strict';

var handshake = require('pull-handshake');

var lp = require('pull-length-prefixed');

var Connection = require('interface-connection').Connection;

var writeEncoded = require('../util.js').writeEncoded;

var some = require('async/some');

function selectHandler(rawConn, handlersMap, log) {
  var cb = function cb(err) {
    // incoming errors are irrelevant for the app
    log.error(err);
  };

  var stream = handshake({
    timeout: 60 * 1000
  }, cb);
  var shake = stream.handshake;
  next();
  return stream;

  function next() {
    lp.decodeFromReader(shake, function (err, data) {
      if (err) {
        return cb(err);
      }

      log('received:', data.toString());
      var protocol = data.toString().slice(0, -1);
      matcher(protocol, handlersMap, function (err, result) {
        if (err) {
          return cb(err);
        }

        var key = result;

        if (key) {
          log('send ack back of: ' + protocol);
          writeEncoded(shake, data, cb);
          var conn = new Connection(shake.rest(), rawConn);
          handlersMap[key].handlerFunc(protocol, conn);
        } else {
          log('not supported protocol: ' + protocol);
          writeEncoded(shake, Buffer.from('na\n'));
          next();
        }
      });
    });
  }
}

function matcher(protocol, handlers, callback) {
  var supportedProtocols = Object.keys(handlers);
  var supportedProtocol = false;
  some(supportedProtocols, function (sp, cb) {
    handlers[sp].matchFunc(sp, protocol, function (err, result) {
      if (err) {
        return cb(err);
      }

      if (result) {
        supportedProtocol = sp;
      }

      cb();
    });
  }, function (err) {
    if (err) {
      return callback(err);
    }

    callback(null, supportedProtocol);
  });
}

module.exports = selectHandler;