'use strict';

var pull = require('pull-stream/pull');

var values = require('pull-stream/sources/values');

var collect = require('pull-stream/sinks/collect');

var pullLP = require('pull-length-prefixed');

var debug = require('debug');

exports = module.exports;

function randomId() {
  return (~~(Math.random() * 1e9)).toString(36);
} // prefixes a message with a varint
// TODO this is a pull-stream 'creep' (pull stream to add a byte?')


function encode(msg, callback) {
  pull(values(Buffer.isBuffer(msg) ? [msg] : [Buffer.from(msg)]), pullLP.encode(), collect(function (err, encoded) {
    if (err) {
      return callback(err);
    }

    callback(null, encoded[0]);
  }));
}

exports.writeEncoded = function (writer, msg, callback) {
  encode(msg, function (err, msg) {
    if (err) {
      return callback(err);
    }

    writer.write(msg);
  });
};

function createLogger(type) {
  var rId = randomId();

  function printer(logger) {
    return function (msg) {
      if (Array.isArray(msg)) {
        msg = msg.join(' ');
      }

      logger('(%s) %s', rId, msg);
    };
  }

  var log = printer(debug('mss:' + type));
  log.error = printer(debug('mss:' + type + ':error'));
  return log;
}

exports.log = {};

exports.log.dialer = function () {
  return createLogger('dialer\t');
};

exports.log.listener = function () {
  return createLogger('listener\t');
};