'use strict';

var errCode = require('err-code');

module.exports = {
  CONNECTION_FAILED: function CONNECTION_FAILED(err) {
    return errCode(err, 'CONNECTION_FAILED');
  },
  DIAL_ABORTED: function DIAL_ABORTED() {
    return errCode('Dial was aborted', 'DIAL_ABORTED');
  },
  ERR_BLACKLISTED: function ERR_BLACKLISTED() {
    return errCode('Dial is currently blacklisted for this peer', 'ERR_BLACKLISTED');
  },
  DIAL_SELF: function DIAL_SELF() {
    return errCode('A node cannot dial itself', 'DIAL_SELF');
  },
  INVALID_STATE_TRANSITION: function INVALID_STATE_TRANSITION(err) {
    return errCode(err, 'INVALID_STATE_TRANSITION');
  },
  NO_TRANSPORTS_REGISTERED: function NO_TRANSPORTS_REGISTERED() {
    return errCode('No transports registered, dial not possible', 'NO_TRANSPORTS_REGISTERED');
  },
  PROTECTOR_REQUIRED: function PROTECTOR_REQUIRED() {
    return errCode('No protector provided with private network enforced', 'PROTECTOR_REQUIRED');
  },
  UNEXPECTED_END: function UNEXPECTED_END() {
    return errCode('Unexpected end of input from reader.', 'UNEXPECTED_END');
  },
  maybeUnexpectedEnd: function maybeUnexpectedEnd(err) {
    if (err === true) {
      return module.exports.UNEXPECTED_END();
    }

    return err;
  }
};