'use strict';

var Connection = require('interface-connection').Connection;

var pull = require('pull-stream/pull');
/**
 * Creates a pull stream to run the given Connection stream through
 * the given Observer. This provides a way to more easily monitor connections
 * and their metadata. A new Connection will be returned that contains
 * has the attached Observer.
 *
 * @param {Transport} transport
 * @param {string} protocol
 * @param {Connection} connection
 * @param {Observer} observer
 * @returns {Connection}
 */


module.exports = function (transport, protocol, connection, observer) {
  var peerInfo = new Promise(function (resolve, reject) {
    connection.getPeerInfo(function (err, peerInfo) {
      if (!err && peerInfo) {
        resolve(peerInfo);
        return;
      }

      var setPeerInfo = connection.setPeerInfo;

      connection.setPeerInfo = function (pi) {
        setPeerInfo.call(connection, pi);
        resolve(pi);
      };
    });
  });
  var stream = {
    source: pull(connection, observer.incoming(transport, protocol, peerInfo)),
    sink: pull(observer.outgoing(transport, protocol, peerInfo), connection)
  };
  return new Connection(stream, connection);
};