'use strict';

var handshake = require('pull-handshake');

var pullLP = require('pull-length-prefixed');

var util = require('./util');

var writeEncoded = util.writeEncoded;

function select(multicodec, callback, log) {
  var stream = handshake({
    timeout: 60 * 1000
  }, callback);
  var shake = stream.handshake;
  log('writing multicodec: ' + multicodec);
  writeEncoded(shake, Buffer.from(multicodec + '\n'), callback);
  pullLP.decodeFromReader(shake, function (err, data) {
    if (err) {
      return callback(err);
    }

    var protocol = data.toString().slice(0, -1);

    if (protocol !== multicodec) {
      return callback(new Error("\"".concat(multicodec, "\" not supported")), shake.rest());
    }

    log('received ack: ' + protocol);
    callback(null, shake.rest());
  });
  return stream;
}

module.exports = select;