'use strict';

var multistream = require('multistream-select');

var observeConn = require('./observe-connection');

var debug = require('debug');

var log = debug('libp2p:switch:protocol-muxer');
log.error = debug('libp2p:switch:protocol-muxer:error');

module.exports = function protocolMuxer(protocols, observer) {
  return function (transport) {
    return function (_parentConn, msListener) {
      var ms = msListener || new multistream.Listener();
      var parentConn; // Only observe the transport if we have one, and there is not already a listener

      if (transport && !msListener) {
        parentConn = observeConn(transport, null, _parentConn, observer);
      } else {
        parentConn = _parentConn;
      }

      Object.keys(protocols).forEach(function (protocol) {
        if (!protocol) {
          return;
        }

        var handler = function handler(protocolName, _conn) {
          log('registering handler with protocol %s', protocolName);
          var protocol = protocols[protocolName];

          if (protocol) {
            var handlerFunc = protocol && protocol.handlerFunc;

            if (handlerFunc) {
              var conn = observeConn(null, protocolName, _conn, observer);
              handlerFunc(protocol, conn);
            }
          }
        };

        ms.addHandler(protocol, handler, protocols[protocol].matchFunc);
      });
      ms.handle(parentConn, function (err) {
        if (err) {
          log.error("multistream handshake failed", err);
        }
      });
    };
  };
};