'use strict';

var Identify = require('libp2p-identify');
/**
 * For a given multistream, registers to handle the given connection
 * @param {MultistreamDialer} multistream
 * @param {Connection} connection
 * @returns {Promise}
 */


module.exports.msHandle = function (multistream, connection) {
  return new Promise(function (resolve, reject) {
    multistream.handle(connection, function (err) {
      if (err) return reject(err);
      resolve();
    });
  });
};
/**
 * For a given multistream, selects the given protocol
 * @param {MultistreamDialer} multistream
 * @param {string} protocol
 * @returns {Promise} Resolves the selected Connection
 */


module.exports.msSelect = function (multistream, protocol) {
  return new Promise(function (resolve, reject) {
    multistream.select(protocol, function (err, connection) {
      if (err) return reject(err);
      resolve(connection);
    });
  });
};
/**
 * Runs identify for the given connection and verifies it against the
 * PeerInfo provided
 * @param {Connection} connection
 * @param {PeerInfo} cryptoPeerInfo The PeerInfo determined during crypto exchange
 * @returns {Promise} Resolves {peerInfo, observedAddrs}
 */


module.exports.identifyDialer = function (connection, cryptoPeerInfo) {
  return new Promise(function (resolve, reject) {
    Identify.dialer(connection, cryptoPeerInfo, function (err, peerInfo, observedAddrs) {
      if (err) return reject(err);
      resolve({
        peerInfo: peerInfo,
        observedAddrs: observedAddrs
      });
    });
  });
};