'use strict';

var pull = require('pull-stream/pull');

var values = require('pull-stream/sources/values');

var lp = require('pull-length-prefixed');

var msg = require('./message');

module.exports = function (conn, pInfoSelf) {
  // send what I see from the other + my Info
  conn.getObservedAddrs(function (err, observedAddrs) {
    if (err) {
      return;
    }

    observedAddrs = observedAddrs[0];
    var publicKey = Buffer.alloc(0);

    if (pInfoSelf.id.pubKey) {
      publicKey = pInfoSelf.id.pubKey.bytes;
    }

    var msgSend = msg.encode({
      protocolVersion: 'ipfs/0.1.0',
      agentVersion: 'na',
      publicKey: publicKey,
      listenAddrs: pInfoSelf.multiaddrs.toArray().map(function (ma) {
        return ma.buffer;
      }),
      observedAddr: observedAddrs ? observedAddrs.buffer : Buffer.from(''),
      protocols: Array.from(pInfoSelf.protocols)
    });
    pull(values([msgSend]), lp.encode(), conn);
  });
};